const handleUpload = (request: any) => {
  // console.log(request.file);
  // setTimeout(() => {
  //   request.onSuccess("ok");
  // }, 0);
  try {
    const reader = new FileReader();
    // if (request.file.type === 'text/csv' || request.file.type === 'text/plain') {
